html {
  text-align: left;
  --main-color: #00acdc;
  font-family: Roboto, Arial, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

body {
  margin: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding-left: 50px;
  padding-right: 50px;
  display: flex;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.logo {
  align-content: center;
  width: 40px;
  height: 40px;
  text-decoration: none;
}

.nav__item {
  margin-right: 20px;
  position: relative;
}

.nav__link {
  color: #000;
  text-transform: uppercase;
  align-content: center;
  height: 60px;
  font-size: 12px;
  text-decoration: none;
  display: block;
}

.last-link {
  margin-right: 0;
}

.is-active {
  color: var(--main-color);
}

.is-active:after {
  content: "";
  background-color: var(--main-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.nav__link:hover {
  color: var(--main-color);
}
